html, body, #root, .App {
  min-height: 100vh;
}

.App {
  text-align: center;
  background-color: #eee;
  color: #000;
  padding-bottom: 50px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.form-container {
  padding-top: 30px;
  padding-left: 20px;
}

#form-search {
  text-align: left;
}

.label-search {
  margin-right: 50px;
}
.label-search input{
  margin-left: 10px;
}
.btn-submit {
  font-size: 15px;
  font-weight: bold;
  padding: 3px 10px 3px 10px;
}

.row-search {
  margin-bottom: 20px;
}

.bit-table {
  border: 1px solid #000;
  border-collapse: collapse;
  min-width: 900px;
  margin-left: 20px;
  margin-right: 20px;
}

.bit-table.header {
  margin-top: 20px;
  position: sticky;
  top:0;
  background-color: #cbddff;
}

.bit-table th, .bit-table td {
  border: 1px solid #000;
}
.bit-table-header {
  display: none;
}

.minus-return {
  color: #0051c7;
}
.plus-return {
  color: #d60000;
}
.bold {
  font-weight: bold !important;
}

.btn-top {
  font-size: 15px;
  font-weight: bold;
  padding: 3px 10px 3px 10px;
  background-color: #fff8c3;
  position: fixed;
  bottom: 10px;
  right: 10px;
  cursor: pointer;
}

.btnExcelDownload {
  background-color: #0d783f;
  color: #fff;
  padding: 4px 10px 4px 10px;
  border: 1px solid #000;
  font-size: 16px;
  margin-left: 100px;
  cursor: pointer;
}
.btnExcelDownload:link {text-decoration: none;}
.btnExcelDownload:visited { text-decoration: none;}